export const PREVIEW_EXAMPLECONTENT = '<h1 class="ql-align-center">Редактор для preview статті (видно на головній)</h1><p class="ql-align-center"><br></p><p class="ql-align-center"><br></p><h2 class="ql-align-center">Приклад статі:</h2><p class="ql-align-center"><br></p><h1 class="ql-align-center">Персоналізоване навчання за допомогою штучного інтелекту</h1><p class="ql-align-center"><br></p><p class="ql-align-center"><img src="data:image/png;base64," width="317" style="">&nbsp; &nbsp; &nbsp;&nbsp;<img src="data:image/jpeg;base64," width="457" style=""></p><p class="ql-align-center"><br></p><p>Штучний інтелект дозволяє створювати персоналізовані програми навчання, враховуючи індивідуальні потреби та темпи розвитку кожного учня. Це сприяє ефективнішому навчанню і розвитку кожної особи відповідно до її властивостей та здібностей.&nbsp;<a href="https://uk.wikipedia.org/wiki/Штучний_інтелект" rel="noopener noreferrer" target="_blank">ШІ</a> використовує аналіз даних та алгоритми для створення персоналізованих навчальних траєкторій. Кожен учень отримує індивідуальну програму, яка враховує його рівень знань, особистість, стиль навчання та темп сприйняття інформації. Такий підхід робить навчання більш ефективним і зорієнтованим на потреби кожного конкретного студента.</p>'
export const MAIN_EXAMPLECONTENT = '<h1 class="ql-align-center">Редактор для всієї статті (коли користувач натискає на статтю)</h1><p class="ql-align-center"><br></p><p>Однією з переваг таких програм є можливість автоматично адаптувати матеріали та завдання в залежності від успіхів учня. Якщо студент виявляється висококваліфікованим у певній області, програма може пропонувати більш складні завдання, щоб підтримати його розвиток. У той час як ті, хто має труднощі, можуть отримувати додаткову підтримку та пояснення.&nbsp;Такий індивідуалізований підхід також враховує різні стилі навчання. Деякі учні краще сприймають інформацію через візуальні засоби, інші – через аудіо чи текст. Персоналізовані програми можуть адаптувати контент, щоб відповідати вподобанням та потребам кожного учня.</p><iframe class="ql-video ql-align-center" frameborder="0" allowfullscreen="true" src="https://www.youtube.com/embed/RGtPR1rHXh4?showinfo=0"></iframe><p class="ql-align-center"><br></p><p><span style="background-color: red;">Однак</span>, разом із перевагами, існують і <strong>виклики</strong>. Важливо забезпечити конфіденційність даних учнів та враховувати можливість формування "фільтру між бульками", де учні отримують тільки те, що відповідає їхнім власним переконанням та інтересам...</p><p class="ql-align-center"><br></p>'